"use client";

const products = [
  { name: "Smartphone", price: "$699" },
  { name: "Headphones", price: "$199" },
  { name: "Sneakers", price: "$129" },
  { name: "Smartwatch", price: "$249" },
];

export default function FeaturedProducts() {
  return (
    <section className="max-w-7xl mx-auto py-12 px-6">
      <h2 className="text-2xl font-bold mb-8">Featured Products</h2>
      <div className="grid grid-cols-2 md:grid-cols-4 gap-6">
        {products.map((product, index) => (
          <div
            key={index}
            className="bg-white p-6 rounded-lg shadow-md hover:shadow-lg cursor-pointer"
          >
            <div className="h-32 bg-gray-200 mb-4 rounded-md"></div>
            <h3 className="font-semibold">{product.name}</h3>
            <p className="text-gray-500">{product.price}</p>
            <button className="mt-3 w-full px-4 py-2 bg-blue-600 text-white rounded-md hover:bg-blue-700 transition">
              Add to Cart
            </button>
          </div>
        ))}
      </div>
    </section>
  );
}
