"use client";

const categories = [
  { name: "Electronics", items: "120+" },
  { name: "Fashion", items: "80+" },
  { name: "Home & Garden", items: "60+" },
  { name: "Sports", items: "40+" },
];

export default function Categories() {
  return (
    <section className="max-w-7xl mx-auto py-12 px-6">
      <h2 className="text-2xl font-bold mb-8">Shop by Category</h2>
      <div className="grid grid-cols-2 md:grid-cols-4 gap-6">
        {categories.map((cat, index) => (
          <div
            key={index}
            className="bg-white p-6 rounded-lg shadow-md hover:shadow-lg cursor-pointer text-center"
          >
            <div className="text-lg font-semibold">{cat.name}</div>
            <p className="text-sm text-gray-500">{cat.items} Items</p>
          </div>
        ))}
      </div>
    </section>
  );
}
